.Header_ggLogOut__1jsCo {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 6px;
  height: 16px;
  border: 2px solid;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  border-right: 0;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  margin-left: -10px;
}
.Header_ggLogOut__1jsCo::after,
.Header_ggLogOut__1jsCo::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
}
.Header_ggLogOut__1jsCo::after {
  border-top: 2px solid;
  border-left: 2px solid;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  width: 8px;
  height: 8px;
  left: 4px;
  bottom: 2px;
}
.Header_ggLogOut__1jsCo::before {
  border-radius: 3px;
  width: 10px;
  height: 2px;
  background: currentColor;
  left: 5px;
  bottom: 5px;
}

